.quoteInfo{
    margin: 40px 0;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    font-family: Lato, sans-serif;
    outline-color: white;
    box-shadow: 8px 8px 8px 8px #888888;
}
.quoteText{
    padding-top: 20px;
    width: 90%;
    display: flex;
    font-size: 30px;
    text-align: center;
}
.quoteAuthor{
    padding-bottom: 10px;
    display: flex;
    width: 90%;
    justify-content: end;
}